import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft'; 
import  MainListItems   from './listItems';
 
import {routes} from './routes';
import { withStyles } from '@material-ui/styles';
import {
 
  Route, Switch, withRouter
} from "react-router-dom"; 



const drawerWidth = 260;

const styles = theme => ({
  root: {
    display: 'flex',
    backgroundColor: '#f5f5f5',
    fontWeight: '300',
    '& a': {
      color: 'white',
      textDecoration: 'none'
    },
    '& > svg': {
      minWidth: '30px',
    },
  },

  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    color: 'white',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    backgroundColor: 'black'
  },

  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    [theme.breakpoints.down('sm')]: {
      marginLeft: '0',
      width: '0',
      transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
  },
  menuButton: {
    marginRight: 36,
    color: 'white',
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    backgroundColor: 'black',
    color: 'white',
    width: drawerWidth,
    borderLeft: 'none',
    paddingRight: '0',
    [theme.breakpoints.down('sm')]: {
      width: '100vw',
    },
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    "& span " :{
      display:'none'
    },
    width: theme.spacing(6),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    overflow: 'auto',

  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
    textAlign: 'center',
    alignItems: "stretch"

  },
  centerAlignedContent: {

    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignSelf: 'center',
    alignItems: 'center',
    minHeight: '56vh',
    textAlign: 'center',
    "h1#{&}": { 
      fontSize: '24px',
      fontWeight: '400',
    }
  },
  heightMax: {
    display: 'flex',
    flex: '1',
    height: '87%'
  },
  fixedHeight: {
    height: '100%', alignItems: "stretch"
  },
  h1: {
    fontWeight: "normal"
  },
  typography: {
    fontWeight: '300',
    button: {
      fontSize: '14px',
      fontFamily: 'Roboto',
      color: 'white'

    },
    h1: {
      fontWeight: "normal",
      fontSize: '24px',

    }
  }
});

class Dashboard extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      open :true,
      setOpen: true  
 }
 
  }

   handleDrawerOpen = () => {
    this.setState({ open: true });
  };
   handleDrawerClose = () => { 
    this.setState({ open: false });
  }; 
  render() {
    const { classes } = this.props;
  
    return (
      <React.Fragment>
    <div className={classes.root}>
     
      <AppBar position="absolute" className={clsx(classes.appBar, this.state.open &&  classes.appBarShift )}>
        <Toolbar className={classes.toolbar}>
          <IconButton
            edge="start"
            color="default"
            fill="white"
            aria-label="open drawer"
            onClick={this.handleDrawerOpen}
            className={clsx(classes.menuButton,  this.state.open &&   classes.menuButtonHidden)}
          >
            <MenuIcon />
          </IconButton>
          <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
         Logo
          </Typography>
         JijoThomas@gmail.com
        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, (this.state.open == false)  &&  classes.drawerPaperClose),
        }}
        open={ this.state.open }
      >
        <div className={classes.toolbarIcon}>
          <IconButton onClick={this.handleDrawerClose}>
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />
        <div id="MenuItems"> <MainListItems  />  </div>
        <Divider />
      </Drawer>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container, classes.heightMax}>
          <Grid container spacing={3}>
            {/* Chart */}
            <Grid item xs={12} md={12} lg={12}>
            <Suspense fallback={<div>Loading...</div>}>
              <Switch>
                {routes.map((route, index, history) => (
                  //      <List><MainListItems></MainListItems></List>
                  // You can render a <Route> in as many places
                  // as you want in your app. It will render along
                  // with any other <Route>s that also match the URL.
                  // So, a sidebar or breadcrumbs or anything else
                  // that requires you to render multiple things
                  // in multiple places at the same URL is nothing
                  // more than multiple <Route>s.
                  <Route
                    key={index}
                    path={route.path}
                    exact={route.exact}
                    component={route.sidebar}
                    history={history}
                    />
                ))}
              </Switch>
              </Suspense>
            </Grid>

          </Grid>
        </Container>
      </main>
    </div>
</React.Fragment>
    )
  }
}  
export default withStyles(styles)( withRouter(Dashboard)); 